.reservation-inputs-container {
  position: absolute;
  top: 18vh;
  left: -4vw; }

.reservation-labels {
  font-size: 20px; }

.reservation-inputs {
  font-size: 20px;
  margin: 10px 0; }

.reservation-start-inputs, .reservation-end-inputs {
  width: 14vw;
  margin: 20px 33px; }

.reservation-start-inputs {
  margin-left: 0;
  margin-right: 50px; }

.reservation-inputs-hour {
  margin-right: 10%; }

.reservation-inputs-ampm {
  margin-right: 20%; }

.available-cars {
  position: absolute;
  top: 13vh;
  width: 100%;
  height: 81.5vh;
  overflow-y: scroll;
  left: 0; }

.available-car {
  border: 1px solid black;
  margin: 2px 0;
  padding: 5px 0; }

.car-img-small {
  width: 150px;
  height: 100px; }

.car-img-large {
  width: 300px;
  height: 200px; }

.reserve-car-info {
  width: 50vw;
  margin: 0 20vw; }



